import React from 'react';
import { Card, Image } from 'semantic-ui-react';

export default class BlogPost extends React.Component {
  render() {
    const text = [];

    if (this.props.paragraphs && this.props.paragraphs.length) {
      for (var i = 0; i < this.props.paragraphs.length; i++) {
        text.push(<p key={i} >
                    { this.props.paragraphs[i] }
                  </p>);
      }
    }

    return <div className='blog-post'>
             <Card>
               { this.props.image &&
                 <Image src={ this.props.image }></Image> }
               <Card.Content>
                 <Card.Header>
                   { this.props.title }
                 </Card.Header>
                 { text }
               </Card.Content>
               <Card.Content extra>
                 {this.props.date}
               </Card.Content>
             </Card>
           </div>;
  }
}



